@charset "UTF-8";
#topMenu {
  width: 100%;
  height: 200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center; }

#logoDiv {
  display: block;
  width: 33%;
  height: 100%; }
  #logoDiv #logo {
    width: 200px;
    height: 200px; }

#widgetDiv {
  display: inline-block;
  width: 33%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end; }
  #widgetDiv div {
    display: block;
    margin: 15px; }
    #widgetDiv div:hover {
      cursor: pointer; }

#contactDiv {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 33%;
  height: 100%;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }
  #contactDiv #emailDiv, #contactDiv #phoneDiv {
    width: 100%;
    text-align: start; }
    #contactDiv #emailDiv img, #contactDiv #phoneDiv img {
      height: 1.5rem; }

#basketCost {
  white-space: pre; }

#logo:hover {
  cursor: pointer;
  -webkit-animation: glare 0.3s ease;
  animation: glare 0.3s ease; }

/*анимация логотипа*/
@-webkit-keyframes glare {
  0% {
    background: -webkit-linear-gradient(315deg, transparent -7%, #e9e9e9 0%, transparent 7%);
    background: linear-gradient(135deg, transparent -7%, #e9e9e9 0%, transparent 7%); }
