  50% {
    background: -webkit-linear-gradient(315deg, transparent 43%, #e9e9e9 50%, transparent 57%);
    background: linear-gradient(135deg, transparent 43%, #e9e9e9 50%, transparent 57%); }
  60% {
    background: -webkit-linear-gradient(315deg, transparent 53%, #e9e9e9 60%, transparent 67%);
    background: linear-gradient(135deg, transparent 53%, #e9e9e9 60%, transparent 67%); }
  70% {
    background: -webkit-linear-gradient(315deg, transparent 63%, #e9e9e9 70%, transparent 77%);
    background: linear-gradient(135deg, transparent 63%, #e9e9e9 70%, transparent 77%); }
  80% {
    background: -webkit-linear-gradient(315deg, transparent 73%, #e9e9e9 80%, transparent 87%);
