  90% {
    background: -webkit-linear-gradient(315deg, transparent 83%, #e9e9e9 90%, transparent 97%);
    background: linear-gradient(135deg, transparent 83%, #e9e9e9 90%, transparent 97%); }
  100% {
    background: -webkit-linear-gradient(315deg, transparent 93%, #e9e9e9 100%, transparent 107%);
    background: linear-gradient(135deg, transparent 93%, #e9e9e9 100%, transparent 107%); } }

